const indexBody = {
    margin: '0 auto',
    paddingTop: '3rem',
    maxWidth: 980,
    flexDirection: 'row',
    justifyContent: 'space-between',
    height: '100%',
};

const indexChildWrapper = {
    margin: '0 auto',
    maxWidth: 980,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    height: '100%',
    padding: '25px',
}

export const styles = {
    indexBody: indexBody,
    indexChildWrapper: indexChildWrapper,
}


